@import url(https://fonts.googleapis.com/css?family=Inconsolata:400,700&display=swap);
body {
  margin: 0;
  font-family: 'Inconsolata';
  color: #2C3E50;
  background: -webkit-gradient(linear, left top, left bottom, from(#FD746C), to(#2C3E50));
  background: linear-gradient(to bottom, #FD746C, #2C3E50);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 25px 25px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  background: white;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

a {
  text-decoration: none;
}

a {
  color: #2C3E50;
}

a:hover {
  color: #FD746C;
}


.Project img {
  width: 500px;
}

h2 {
  padding: 30px 0;
}

.project-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.project-title {
  font-size: 20px;
  letter-spacing: 2px;
  text-align: center;
}

.project-category {
  font-size: 18px;
  text-align: center;

}

.project-description {
  font-size: 15px;
  text-align: center;
} 
h1 {
  font-size: 35px;
}

h2 {
  font-size: 30px;
}

p {
  font-size: 20px;
}

.container {
  margin: 10px 150px;
  padding-bottom: 10px;
}

.description {
  margin-left: 10px;
}
.about-container {
    margin: 10px 150px;
    padding-bottom: 10px;
}

p {
    font-size: 15px;
}

.main-info {
    font-size: 18px;
    font-weight: bold;
    margin-right: 10px;
    line-height: 20px;
}

.header {
    padding: 10px 0px;
}

.sub-info {
    padding-left: 15px;
}

.highlight-header {
    position: relative;
    z-index: 1;
}

.highlight-header:after {
    background-color: #FD746C;
    bottom: 0;
    content: ' ';
    display: block;
    height: 49%;
    left: 15px;
    opacity: 0.8;
    position: absolute;
    width: calc(15%);
    z-index: -1;
}
.Nav__container {
  display: flex;
  justify-content: space-between;
  /* border-bottom: solid 1px #FD746C; */
  padding: 50px 0px;
  margin: 0px 150px;
}

.Links {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin: 0;
  min-width: 0;
  flex-flow: wrap;
}

.Nav img {
  width: 150px;
  height: 70px;
}

.item {
  margin: 10px;
}

.Nav ul {
  list-style-type: none;
  display: flex;
}

.active {
  position: relative;
  z-index: 1;
  font-weight: bold;
}

.active:hover {
  color: #2C3E50;
}

.active:after {
  background-color: #FD746C;
  content: ' ';
  height: 50%;
  opacity: 1;
  position: absolute;
  opacity: 0.8;
  right: 7px;
  top: 0;
  width: 100%;
  z-index: -1;
}
